import React, { useState } from "react";

const Settings = () => {
  const [activeTab, setActiveTab] = useState("profile");

  return (
    <div className="container py-5">
      {/* Header */}
      <h2 className="mb-4">Ci t</h2>

      {/* Tabs */}
      <ul className="nav nav-tabs mb-4">
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === "profile" ? "active" : ""}`}
            onClick={() => setActiveTab("profile")}
          >
            H s
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === "password" ? "active" : ""}`}
            onClick={() => setActiveTab("password")}
          >
            Mt khu
          </button>
        </li>
        <li className="nav-item">
          <button
            className={`nav-link ${activeTab === "social" ? "active" : ""}`}
            onClick={() => setActiveTab("social")}
          >
            H s x hi
          </button>
        </li>
      </ul>

      {/* Tab Content */}
      <div>
        {activeTab === "profile" && (
          <div className="col-lg-9">
          {/* Header */}
          <h2 className="mb-4">Ci t</h2>
    
          {/* Banner and Profile Picture */}
          <div className="mb-4 position-relative">
{/* Banner */}
<img
  src="https://covuadaisy.com/wp-content/plugins/tutor/assets/images/cover-photo.jpg"
  alt="Banner"
  className="w-100 rounded"
/>
<button className="btn btn-primary position-absolute bottom-0 end-0 m-3">
  Ti ln nh ba
</button>

{/* Avatar */}
<div
  className="position-absolute top-50 start-0 translate-middle-y"
  style={{ zIndex: 1, marginLeft: "15px" }}
>
  <img
    src="https://via.placeholder.com/100"
    alt="Avatar"
    className="rounded-circle border border-3"
    style={{ width: "100px", height: "100px" }}
  />
</div>
</div>

    
          {/* Form */}
          <form>
            <div className="row mb-3">
              <div className="col-md-6">
                <label htmlFor="lastName" className="form-label">
                  Tn
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="lastName"
                  placeholder="H"
                />
              </div>
              <div className="col-md-6">
                <label htmlFor="firstName" className="form-label">
                  H
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="firstName"
                  placeholder="Tn"
                />
              </div>
            </div>
    
            <div className="row mb-3">
              <div className="col-md-6">
                <label htmlFor="email" className="form-label">
                  Tn ng nhp
                </label>
                <input
                  type="email"
                  className="form-control"
                  id="email"
                  placeholder="example@example.com"
                />
              </div>
              <div className="col-md-6">
                <label htmlFor="phone" className="form-label">
                  S in thoi
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="phone"
                  placeholder="S in thoi"
                />
              </div>
            </div>
    
            <div className="row mb-3">
              <div className="col-md-6">
                <label htmlFor="job" className="form-label">
                  K nng / Ngh nghip
                </label>
                <select id="job" className="form-select">
                  <option value="">Chn ngh nghip</option>
                  <option value="IT">IT</option>
                  <option value="Teacher">Teacher</option>
                  <option value="Doctor">Doctor</option>
                </select>
              </div>
              <div className="col-md-6">
                <label htmlFor="status" className="form-label">
                  Trng thi
                </label>
                <select id="status" className="form-select">
                  <option value="">Trng thi</option>
                  <option value="Active">Hot ng</option>
                  <option value="Inactive">Khng hot ng</option>
                </select>
              </div>
            </div>
    
            <div className="mb-3">
              <label htmlFor="bio" className="form-label">
                Tiu s
              </label>
              <textarea
                id="bio"
                className="form-control"
                rows="4"
                placeholder="Thm tiu s..."
              ></textarea>
            </div>
    
            <div className="mb-3">
              <label htmlFor="location" className="form-label">
                Tn hin th cng khai
              </label>
              <select id="location" className="form-select">
                <option value="">Chn tn hin th</option>
                <option value="Thun Hunh">Thun Hunh</option>
                <option value="Admin">Admin</option>
              </select>
            </div>
    
            <button type="submit" className="btn btn-primary">
              Cp nht thng tin
            </button>
          </form>
        </div>
        )}

        {activeTab === "password" && (
          <div>
            <h3>Mt khu</h3>
            {/* Ni dung tab Mt khu */}
            <form>
              <div className="mb-3">
                <label htmlFor="currentPassword" className="form-label">
                  Mt khu hin ti
                </label>
                <input
                  type="password"
                  className="form-control"
                  id="currentPassword"
                  placeholder="Nhp mt khu hin ti"
                />
              </div>
              <div className="mb-3">
                <label htmlFor="newPassword" className="form-label">
                  Mt khu mi
                </label>
                <input
                  type="password"
                  className="form-control"
                  id="newPassword"
                  placeholder="Nhp mt khu mi"
                />
              </div>
              <button type="submit" className="btn btn-primary">
                i mt khu
              </button>
            </form>
          </div>
        )}

        {activeTab === "social" && (
          <div>
            <h3>H s x hi</h3>
            {/* Ni dung tab H s x hi */}
            <form>
              <div className="mb-3">
                <label htmlFor="facebook" className="form-label">
                  Facebook
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="facebook"
                  placeholder="URL Facebook"
                />
              </div>
              <div className="mb-3">
                <label htmlFor="twitter" className="form-label">
                  Twitter
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="twitter"
                  placeholder="URL Twitter"
                />
              </div>
              <button type="submit" className="btn btn-primary">
                Lu thay i
              </button>
            </form>
          </div>
        )}
      </div>
    </div>
  );
};

export default Settings;
