import React from "react";
import "../css/CourseList.css"
import Header from "../Component/Header";
import Footer from "../Component/Footer";
const CourseList = () => {

    const courses = [
        {
          id: 1,
          title: "C tn xe nng cao | Master Class",
          image: "https://covuadaisy.com/wp-content/uploads/2024/04/Banner-2-3.png",
          instructor: "Daisy C Vua",
          category: "Master Class",
          duration: "8h 10m",
          price: {
            original: 900000,
            discounted: 399000,
          },
          rating: 5.0,
          reviews: 1,
        },
        {
          id: 2,
          title: "Cng B Khm Ph Th Gii C Vua",
          image: "https://covuadaisy.com/wp-content/uploads/2024/12/Banner-1.png",
          instructor: "Daisy C Vua",
          category: "Hc c bn",
          duration: "10h",
          price: {
            original: 999000,
            discounted: 599000,
          },
          rating: 5.0,
          reviews: 1,
        },
        {
          id: 3,
          title: "Phng th c bn dnh cho ngi mi bt u",
          image: "https://covuadaisy.com/wp-content/uploads/2024/12/Banner-7.png",
          instructor: "Daisy C Vua",
          category: "C bn",
          duration: "5h 20m",
          price: {
            original: 700000,
            discounted: 299000,
          },
          rating: 5.0,
          reviews: 2,
        },
        {
          id: 4,
          title: "Chin thut v khai cuc nng cao",
          image: "https://covuadaisy.com/wp-content/uploads/2024/12/Banner-7.png",
          instructor: "Daisy C Vua",
          category: "Nng cao",
          duration: "12h 15m",
          price: {
            original: 1200000,
            discounted: 899000,
          },
          rating: 4.8,
          reviews: 5,
        },
        {
          id: 5,
          title: "Pht trin chin lc trong trung cuc",
          image: "https://covuadaisy.com/wp-content/uploads/2024/12/Banner-7.png",
          instructor: "Daisy C Vua",
          category: "Trung cp",
          duration: "9h 30m",
          price: {
            original: 1100000,
            discounted: 799000,
          },
          rating: 4.9,
          reviews: 3,
        },
        {
          id: 6,
          title: "Khm ph cc nc c sng to | Master Class",
          image: "https://covuadaisy.com/wp-content/uploads/2024/07/Banner-6.png",
          instructor: "Daisy C Vua",
          category: "Master Class",
          duration: "15h",
          price: {
            original: 1500000,
            discounted: 1099000,
          },
          rating: 4.7,
          reviews: 4,
        },
      ];
  return (
    <div>
        <Header/>
        <div className="container mt-4">
      <div className="row">
        {/* Ct B lc */}
        <div className="col-md-3 mb-2">
          <div className="card shadow-sm border-0">
            <div className="card-header  text-white text-center">
              <h5 className="mb-0"></h5>
            </div>
            <div className="card-body">
              <form>
                {/* Tm kim */}
                <div className="mb-4">
      <div className="input-group">
        <span className="input-group-text">
          <i className="bi bi-search"></i> {/* Bootstrap Icon */}
        </span>
        <input
          type="search"
          className="form-control"
          id="search"
          placeholder="Nhp t kha"
        />
      </div>
    </div>

                {/* B lc Type */}
                <div className="mb-4">
                  <h6 className="text-primary fw-bold" style={{ display: "flex" }}>Type</h6>
                  <div className="form-check d-flex align-items-center">
  <input
    className="form-check-input me-2" // Thm "me-2"  to khong cch nh (nu cn)
    type="checkbox"
    id="course"
    value="course"
  />
  <label className="form-check-label" htmlFor="course">
    Course
  </label>
</div>
<div className="form-check d-flex align-items-center">
  <input
    className="form-check-input me-2"
    type="checkbox"
    id="bundle"
    value="bundle"
  />
  <label className="form-check-label mb-0" htmlFor="bundle">
    Bundle
  </label>
</div>

                </div>

                {/* B lc Loi */}
                <div className="mb-4">
  <h6 className="text-primary fw-bold" style={{ display: "flex" }}>Loi</h6>
  {[
    "Bi tp",
    "Bn Ngoi C vua",
    "Chin lc",
    "Chin thut",
    "Chiu ht",
    "Hoc c c ban",
    "Hc qua sch",
    "Khai cuc",
    "Master Class",
    "Opening Repertoire",
    "Tn cuc",
    "Tnh ton",
  ].map((item, index) => (
    <div className="form-check d-flex align-items-center" key={index}>
      <input
        className="form-check-input me-2"
        type="checkbox"
        id={`category${index}`}
        value={`category${index}`}
      />
      <label
        className="form-check-label mb-0"
        htmlFor={`category${index}`}
      >
        {item}
      </label>
    </div>
  ))}
</div>


              {/* B lc Mc  */}
<div className="mb-4">
  <h6 className="text-primary fw-bold" style={{ display: "flex" }}>Mc </h6>
  {["Tt c trnh ", "S cp", "Trung cp", "Cao cp"].map(
    (level, index) => (
      <div className="form-check d-flex align-items-center" key={index}>
        <input
          className="form-check-input me-2"
          type="checkbox"
          id={`level${index}`}
          value={`level${index}`}
        />
        <label
          className="form-check-label mb-0"
          htmlFor={`level${index}`}
        >
          {level}
        </label>
      </div>
    )
  )}
</div>


                {/* B lc Gi */}
                <div className="mb-4">
  <h6 className="text-primary fw-bold" style={{ display: "flex" }}>Gi</h6>
  <div className="form-check d-flex align-items-center">
    <input
      className="form-check-input me-2"
      type="checkbox"
      id="free"
      value="free"
    />
    <label className="form-check-label mb-0" htmlFor="free">
      Min ph
    </label>
  </div>
  <div className="form-check d-flex align-items-center">
    <input
      className="form-check-input me-2"
      type="checkbox"
      id="paid"
      value="paid"
    />
    <label className="form-check-label mb-0" htmlFor="paid">
      Tr ph
    </label>
  </div>
</div>


                {/* Nt xa b lc */}
                <div className="text-center">
                  <button
                    type="button"
                    className="btn btn-danger btn-sm"
                    onClick={() => window.location.reload()}
                  >
                    Xa tt c b lc
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>

        {/* Ct hin th ni dung khc */}
        <div className="col-md-9 mb-2">
        <div className="course-grid">
        {courses.map((course) => (
          <div key={course.id} className="course-card">
            <div className="course-thumbnail">
              <img src={course.image} alt={course.title} />
            </div>
            <div className="course-details">
            <a href="/course-detail" style={{ textDecoration: "none" }}>
  <h3 className="course-title">{course.title}</h3>
</a>

         
              <p>Ging vin: {course.instructor}</p>
              <p>Thi lng: {course.duration}</p>
              <p>Danh mc: {course.category}</p>
              <div className="course-rating">
                {[...Array(5)].map((_, index) => (
                  <span
                    key={index}
                    style={{
                      color: index < Math.round(course.rating) ? '#FFD700' : '#dcdcdc',
                    }}
                  >
                    
                  </span>
                ))}
              </div>
              <div className="course-price">
                <div>
                  <span className="original-price">{course.price.original.toLocaleString()}</span>
                 <br/>
                  <span className="discounted-price">{course.price.discounted.toLocaleString()}</span>
                </div>
                <button className="buy-course-btn">Mua kha hc</button>
              </div>
            </div>
          </div>
        ))}
      </div>
        </div>
      </div>
    </div>
        <Footer/>
    </div>
  );
};

export default CourseList;
