import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from '../Component/Header';
import Footer from '../Component/Footer';

const CourseDetail = () => {
    const course = {
        id: 1,
        title: "Chinh phc Vienna  Khai cuc c o nht trong c vua.",
        image: "https://covuadaisy.com/wp-content/uploads/2024/04/Banner-2-3.png",
        instructor: "Daisy C Vua",
        category: "Chin lc, Khai cuc",
        duration: "3 gi 20 pht",
        price: 229000,
        rating: 5.0,
        reviews: 3,
        level: "Trung cp",
        students: 14,
        updateDate: "16 Thng Chn 2024",
        certificate: true,
        contents: [
          {
            title: "Gii thiu khai cuc Vienna.",
            lessons: [
              { title: "Ti sao li hc Vienna.", duration: "02:32", locked: true },
              { title: "Cc phng n ca Vienna.", duration: "02:54", locked: true },
            ],
          },
          { title: "Phng n: Max Lange Defense." },
          { title: "Phng n Anderssen." },
          { title: "Vienna Gambit." },
          { title: "Phng n Mieses." },
        ],
      };

  const [activeTab, setActiveTab] = useState('description');

  return (
   <div>
    <Header/>
    <div className="container mt-5">
      <div className="row">
        {/* Left Section */}
        <div className="col-md-8">
          <h1 className="fw-bold text-primary mb-3">{course.title}</h1>
          <div className="d-flex align-items-center mb-3">
            {[...Array(5)].map((_, index) => (
              <span
                key={index}
                style={{
                  color: index < Math.round(course.rating) ? '#FFD700' : '#dcdcdc',
                  fontSize: '20px',
                }}
              >
                
              </span>
            ))}
            <span className="ms-2 text-muted">{course.rating} ({course.reviews} nh gi)</span>
          </div>
          <p className="text-secondary">Danh mc: {course.category}</p>

          <div className="mt-4">

          <div>
                <img src={course.image} alt={course.title} className="img-fluid rounded shadow mb-4" />
               
              </div>
              <ul className="nav nav-tabs mt-5">
            <li className="nav-item">
              <button
                className={`nav-link ${activeTab === 'description' ? 'active' : ''}`}
                onClick={() => setActiveTab('description')}
              >
                Gii thiu
              </button>
            </li>
            <li className="nav-item">
              <button
                className={`nav-link ${activeTab === 'reviews' ? 'active' : ''}`}
                onClick={() => setActiveTab('reviews')}
              >
                nh gi
              </button>
            </li>
          </ul>
            {activeTab === 'description' && (
           <div className='mb-2'>

<div>
                <h5 className="fw-bold mt-2">Gii thiu v kha hc</h5>
                <p className="text-justify">
                  Kha hc ny s gip bn lm ch Khai cuc Vienna  mt la chn tinh t v y sng to dnh cho ngi chi c vua mun khai thc cc th trn c o. Vi nc i 1.e4 e5 2.Mc3, Khai cuc Vienna khng ch m ra nhng cuc tn cng mnh m m cn to iu kin cho s k hoch sng to t c hai pha.
                </p>
                <p className="text-justify">
                  <strong>Trong kha hc, bn s:</strong>
                  <ul>
                    <li>Hiu r cc bin th chnh ca Khai cuc Vienna v cch tn dng li th trong tng tnh hung.</li>
                    <li>Khm ph nhng phng n tn cng sc bn nh Gambit Vienna, gip to sc p mnh m ln i th t nhng nc i u tin.</li>
                    <li>Tm hiu cch phn ng linh hot v x l nhng tnh hung phc tp khi i th khng theo cc nc i thng thng.</li>
                    <li>Phn tch cc trn u thc t v chin lc ca cc k th hng u khi p dng Khai cuc Vienna.</li>
                  </ul>
                </p>
                <p className="text-justify">
                  Kha hc ph hp vi nhng ngi chi t cp trung bnh tr ln, mun nng cao k nng khai cuc v lm phong ph thm phong cch chi ca mnh.
                </p>
              </div>
          

              <div>
                <h5 className="fw-bold">Ni dung kha hc</h5>
                <div className="accordion" id="courseContent">
                  {course.contents.map((content, index) => (
                    <div className="accordion-item" key={index}>
                      <h2 className="accordion-header" id={`heading${index}`}>
                      <h2 className="accordion-header" id={`heading${index}`}>
                        <button
                          className="accordion-button"
                          type="button"
                          data-bs-toggle="collapse"
                          data-bs-target={`#collapse${index}`}
                          aria-expanded="true"
                          aria-controls={`collapse${index}`}
                        >
                          {content.title}
                        </button>
                      </h2>
                      </h2>
                      {content.lessons && (
                        <div
                          id={`collapse${index}`}
                          className="accordion-collapse collapse show"
                          aria-labelledby={`heading${index}`}
                          data-bs-parent="#courseContent"
                        >
                    <div className="accordion-body">
  <ul className="list-unstyled">
    {content.lessons.map((lesson, lessonIndex) => (
      <li
        key={lessonIndex}
        className="d-flex justify-content-between align-items-center py-2"
      >
        <div className="d-flex align-items-center">
          <i className="bi bi-play-circle-fill text-primary me-2"></i>
          <span>{lesson.title}</span>
        </div>
        <div className="d-flex align-items-center">
          <span className="text-muted small">{lesson.duration}</span>
          {lesson.locked && (
            <i
              className="bi bi-lock-fill ms-2 text-muted"
              title="Locked"
            ></i>
          )}
        </div>
      </li>
    ))}
  </ul>
</div>

                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>
           </div>
            )}
            {activeTab === 'reviews' && (
              <div className='mb-2'>
               <div>
                <h5 className="fw-bold mt-2">nh gi</h5>
                <div className="card p-3 mb-3 shadow-sm">
                  <div className="d-flex align-items-center mb-2">
                    <img
                      src="https://covuadaisy.com/wp-content/uploads/2024/09/bobby-fischer-vaultjpg-1-150x150.jpg"
                      alt="V Duy Win"
                      className="rounded-circle me-3"
                      style={{ width: '50px', height: '50px' }}
                    />
                    <div>
                      <h6 className="mb-0">V Duy Win</h6>
                      <small className="text-muted">4 thng trc</small>
                    </div>
                  </div>
                  <p className="mb-1">kho hc hay, c th s l khai cuc t ca em.</p>
                  <div className="text-warning"></div>
                </div>

                <div className="card p-3 mb-3 shadow-sm">
                  <div className="d-flex align-items-center mb-2">
                    <span className="rounded-circle bg-primary text-white d-flex align-items-center justify-content-center me-3" style={{ width: '50px', height: '50px' }}>BT</span>
                    <div>
                      <h6 className="mb-0">Bi Tun</h6>
                      <small className="text-muted">4 thng trc</small>
                    </div>
                  </div>
                  <p className="mb-1">Khai cuc tng c bn nhng nhiu by mnh. Kho hc hay.</p>
                  <div className="text-warning"></div>
                </div>

                <div className="card p-3 shadow-sm">
                  <div className="d-flex align-items-center mb-2">
                    <img
                      src="https://covuadaisy.com/wp-content/uploads/2024/07/anh-dai-dien-150x150.jpg"
                      alt="Nguyn Thanh Hu"
                      className="rounded-circle me-3"
                      style={{ width: '50px', height: '50px' }}
                    />
                    <div>
                      <h6 className="mb-0">Nguyn Thanh Hu</h6>
                      <small className="text-muted">4 thng trc</small>
                    </div>
                  </div>
                  <p className="mb-1">Nu bn thy kho hc hay, hy cho mnh 5 sao nh!</p>
                  <div className="text-warning"></div>
                </div>
              </div>
              </div>
            )}
          </div>

          {/* Tabs */}
         
        </div>

        {/* Right Section */}
        <div className="col-md-4">
          <div className="card shadow border-0">
            <div className="card-body">
              <h3 className="text-danger fw-bold text-center mb-3">{course.price.toLocaleString()}</h3>
              <button className="btn btn-primary w-100 mb-3">Mua kha hc</button>
              <ul className="list-unstyled">
                <li className="mb-2 d-flex align-items-center">
                  <i className="bi bi-bar-chart me-2 text-primary"></i> {course.level}
                </li>
                <li className="mb-2 d-flex align-items-center">
                  <i className="bi bi-people me-2 text-primary"></i> {course.students} Tng s hc vin
                </li>
                <li className="mb-2 d-flex align-items-center">
                  <i className="bi bi-clock me-2 text-primary"></i> {course.duration} Thi lng
                </li>
                <li className="mb-2 d-flex align-items-center">
                  <i className="bi bi-calendar3 me-2 text-primary"></i> Cp nht: {course.updateDate}
                </li>
                {course.certificate && (
                  <li className="d-flex align-items-center">
                    <i className="bi bi-patch-check me-2 text-primary"></i> Certificate of completion
                  </li>
                )}
              </ul>
            </div>
          </div>
          <div className="card mt-4 shadow border-0">
            <div className="card-body">
            
              <div className="mb-4">
                <h6 className="fw-bold" style={{ display: "flex" }}>Kha hc bi</h6>
                <div className="d-flex align-items-center">
                  <img
                    src="https://covuadaisy.com/wp-content/uploads/2024/04/logo-den-150x150.jpg"
                    alt="Daisy C Vua"
                    className="rounded-circle me-3"
                    style={{ width: '50px', height: '50px' }}
                  />
                  <div>
                    <a
                      style={{ display: "flex" }}
                      className="text-decoration-none fw-bold text-dark"
                    >
                      Daisy C Vua
                    </a>
                    <p className="text-muted small mb-0">Gio vin c vua - Hun Luyn vin Quc Gia</p>
                  </div>
                </div>
              </div>
              <div className="mb-4">
                <h6 className="fw-bold">Cc ti liu kho hc</h6>
                <ul className="list-unstyled">
                  <li className="d-flex align-items-center mb-2">
                    <i className="bi bi-file-earmark-text-fill text-primary me-2"></i>
                    Bi ging
                  </li>
                  <li className="d-flex align-items-center">
                    <i className="bi bi-file-earmark-text-fill text-primary me-2"></i>
                    Bi tp
                  </li>
                </ul>
              </div>
              <div className="mb-4">
                <h6 className="fw-bold">T kho</h6>
                <div className="d-flex flex-wrap">
                  <a href="#" className="badge bg-primary text-white me-2 mb-2">Anderssen</a>
                  <a href="#" className="badge bg-primary text-white me-2 mb-2">By khai cuc Vienna</a>
                  <a href="#" className="badge bg-primary text-white mb-2">Max Lange</a>
                </div>
              </div>
              <div>
                <h6 className="fw-bold">i tng</h6>
                <ul className="list-unstyled">
                  <li className="d-flex align-items-center">
                    <i className="bi bi-people-fill text-primary me-2"></i>
                    Nhng bn rating 1200 - 2100
                  </li>
                </ul>
              </div>
            </div>
          </div>
        
        </div>

        
      </div>
    </div>
    <Footer/>
   </div>
  );
};

export default CourseDetail;
