import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

const ArticleDetail = () => {
  const [comments, setComments] = useState([]); // Lu tr cc bnh lun
  const [newComment, setNewComment] = useState(""); // Lu ni dung bnh lun mi

  // X l gi bnh lun
  const handleCommentSubmit = (e) => {
    e.preventDefault();
    if (newComment.trim()) {
      setComments([...comments, newComment]); // Thm bnh lun mi vo danh sch
      setNewComment(""); // Xa ni dung trong  nhp
    }
  };

  return (
    <div className="container my-5 p-4 bg-light rounded shadow">
      {/* Tiu  bi vit */}
      <header className="mb-4 text-center">
        <h1 className="fw-bold">Cc gii c vua Quc Gia 2024</h1>
        <p className="text-muted">
          By <a href="https://covuadaisy.com/author/covuadaisy-com/">Daisy C Vua</a> / 15 Thng T 2024
        </p>
      </header>

      {/* Hnh nh chnh */}
      <div className="mb-4 text-center">
        <img
          src="https://covuadaisy.com/wp-content/uploads/2024/04/giai-2024-1024x538.jpg"
          alt="Cc gii c vua Quc Gia 2024"
          className="img-fluid rounded shadow-lg"
        />
      </div>

      {/* Ni dung bi vit */}
      <section>
        {[
          {
            title: "Gii c vua ng bng sng Cu Long m rng",
            time: "13 thng 2 n ngy 16 thng 2",
            location: "ng Thp",
          },
          {
            title: "Gii c vua tr Min Trung m rng",
            time: "17 thng 2 n ngy 21 thng 2",
            location: " Nng",
          },
          {
            title: "Gii c vua v ch Quc Gia",
            time: "01 thng 03 n ngy 11 thng 3",
            location: "H Ni",
          },
        ].map((item, index) => (
          <div key={index} className="mb-4 p-3 bg-white rounded shadow-sm">
            <h4 className="fw-bold text-primary">{item.title}</h4>
            <p className="mb-0">
              <strong>Thi gian:</strong> {item.time}
            </p>
            <p>
              <strong>a im:</strong> {item.location}
            </p>
          </div>
        ))}
      </section>

      {/* Khu vc bnh lun */}
      <div id="comments" className="comments-area mt-5">
        <h3 className="mb-4">Bnh lun</h3>

        {/* Hin th cc bnh lun */}
        {comments.length > 0 && (
          <div className="mb-4">
            <h4 className="mb-3">Bnh lun ({comments.length})</h4>
            <ul className="list-group">
              {comments.map((comment, index) => (
                <li key={index} className="list-group-item">
                  {comment}
                </li>
              ))}
            </ul>
          </div>
        )}

        {/* Form bnh lun */}
        <form onSubmit={handleCommentSubmit} className="p-4 bg-white rounded shadow-sm">
          <div className="mb-3">
            <label htmlFor="comment" className="form-label">
              Bnh lun ca bn
            </label>
            <textarea
              id="comment"
              className="form-control"
              rows="4"
              value={newComment}
              onChange={(e) => setNewComment(e.target.value)}
              placeholder="Nhp bnh lun ca bn..."
              required
            ></textarea>
          </div>
          <button type="submit" className="btn btn-primary">
            Gi bnh lun
          </button>
        </form>
      </div>
    </div>
  );
};

export default ArticleDetail;
