import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Header from "../Component/Header";
import Footer from "../Component/Footer";

const ForgotPassword = () => {
  const [emailOrUsername, setEmailOrUsername] = useState("");
  const [message, setMessage] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();

    // X l gi yu cu qun mt khu  y
    if (emailOrUsername.trim() === "") {
      setMessage("Vui lng nhp tn ng nhp hoc a ch email.");
      return;
    }

    setMessage("Thng tin to li mt khu  c gi qua email ca bn.");
    setEmailOrUsername("");
  };

  return (
    <div>
 <Header/>
<div className="container ">
       
      <div className="row justify-content-center py-5">
        <div className="col-md-6">
          <div className="card shadow">
            <div className="card-body">
              <h3 className="text-center mb-4">Qun Mt Khu</h3>
              <p>
                Qun mt khu? Vui lng nhp tn ng nhp hoc a ch email. Bn s
                nhn c thng tin to li mt khu qua email ca bn.
              </p>

              {message && (
                <div className="alert alert-info text-center" role="alert">
                  {message}
                </div>
              )}

              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label htmlFor="user_login" className="form-label">
                    Tn ng nhp hoc a ch email
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="user_login"
                    name="user_login"
                    placeholder="Nhp tn ng nhp hoc email"
                    value={emailOrUsername}
                    onChange={(e) => setEmailOrUsername(e.target.value)}
                  />
                </div>
                <div className="d-grid">
                  <button
                    type="submit"
                    className="btn btn-primary"
                  >
                    t li mt khu
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer/>
    </div>
  );
};

export default ForgotPassword;
