import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from '../Component/Header';
import Footer from '../Component/Footer';

const TournamentDetails = () => {
  const [activeTable, setActiveTable] = useState(1); // Trng thi hin th bng (vn 1 hoc vn khc)

  const matchesTable1 = [
    { board: 1, white: 'Bo Khang', result: '1-0', black: 'Khi Minh' },
    { board: 2, white: 'Khnh Tng', result: '1-0', black: 'ng Nguyn' },
    { board: 3, white: 'ng Nhn', result: '0-1', black: 'Khi Nguyn' },
  ];

  const matchesTable2 = [
    { board: 1, white: 'Minh Quang', result: '0-1', black: 'H An Nhin' },
    { board: 2, white: 'Hong Khnh', result: '0-1', black: 'Minh Sn' },
    { board: 3, white: 'Phc Khang', result: '1-0', black: 'Hu ' },
  ];

  const rankings = [
    { rank: 1, name: 'Minh Sn', points: 5.5 },
    { rank: 2, name: 'Khi Nguyn', points: 5 },
    { rank: 3, name: 'ng Nhn', points: 4.5 },
    { rank: 4, name: 'Khi Minh', points: 4.5 },
    { rank: 5, name: 'Hi Phong', points: 4 },
    { rank: 6, name: 'Khnh Tng', points: 4 },
    { rank: 7, name: 'H An Nhin', points: 3.5 },
    { rank: 8, name: 'Bo Khang', points: 3.5 },
    { rank: 9, name: 'Minh Hi', points: 3.5 },
    { rank: 10, name: 'Gia Anh', points: 3 },
    { rank: 11, name: 'Minh Khang', points: 3 },
    { rank: 12, name: 'Hong Khnh', points: 3 },
    { rank: 13, name: 'Phc Khang', points: 3 },
  ];
  return (
    <div>
<Header/>
<div className="container py-5">
      {/* Phn tiu  trn cao */}
      <div
        className="text-center mb-5"
        style={{
          fontSize: '1.8rem',
          fontWeight: 'bold',
          color: '#dc3545',
          textShadow: '1px 1px 2px rgba(0,0,0,0.2)',
        }}
      >
        Gii u Ngy 21.12.2024
      </div>

      {/* Card chnh */}
      <section
        className="mb-5"
        style={{
          background: '#000',
          color: '#fff',
          borderRadius: '15px',
          boxShadow: '0 8px 15px rgba(0, 0, 0, 0.3)',
          padding: '30px',
          textAlign: 'center',
        }}
      >
        <div
          style={{
            fontSize: '1.2rem',
            letterSpacing: '1px',
            textTransform: 'uppercase',
            background: '#333',
            display: 'inline-block',
            padding: '5px 15px',
            borderRadius: '20px',
            marginBottom: '15px',
            color: '#fff',
          }}
        >
          CLB Daisy Chess
        </div>
        <h1
          style={{
            fontSize: '3rem',
            fontWeight: '700',
            background: 'linear-gradient(90deg, #ff7b54, #ffa726)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            margin: '15px 0',
          }}
        >
          Luyn tp
        </h1>
        <h2
          style={{
            fontSize: '1.8rem',
            fontWeight: 'bold',
            color: '#fff',
            textShadow: '1px 1px 2px rgba(0,0,0,0.5)',
          }}
        >
          21.12.2024
        </h2>
      </section>

      {/* Phn ni dung chi tit */}
      <div className="row align-items-center justify-content-center text-center">
  {/* Ct nh */}
  <div className="col-md-4 mb-4 mb-md-0">
    <img
      src="https://covuadaisy.com/wp-content/uploads/2024/12/banner-6.png"
      alt="Daisy Chess"
      className="img-fluid rounded shadow"
      style={{ maxWidth: '100%' }}
    />
  </div>

  {/* Ct thng tin */}
  <div className="col-md-8">
    <h2 className="text-danger fw-bold mb-3">Th thc thi u</h2>
    <ul className="list-unstyled">
      <li className="mb-3 d-flex align-items-center justify-content-center">
        <i className="bi bi-clock-fill me-2 text-danger"></i>
        <span className="fw-bold">Thi gian:</span> 10p+2s
      </li>
      <li className="mb-3 d-flex align-items-center justify-content-center">
        <i className="bi bi-bullseye me-2 text-primary"></i>
        <span className="fw-bold">S vn:</span> 6 vn
      </li>
      <li className="d-flex align-items-center justify-content-center">
        <i className="bi bi-geo-alt-fill me-2 text-success"></i>
        <span className="fw-bold">a ch:</span> 49 Thanh Lng 11
      </li>
    </ul>
  </div>
</div>

<div className="border-top border-dark mb-4"></div>

      {/* Tiu  */}
      <h2 className="text-center text-warning fw-bold mb-4">
        Danh sch vn ng vin
      </h2>

      {/* Danh sch */}
      <div className="row text-center">
        {/* Ct 1 */}
        <div className="col-md-4">
          <p>
            <strong>
              Ln Nam<br />
              Duy Anh<br />
              Hong Khnh<br />
              Minh Hi<br />
              ng Nguyn<br />
              Gia Bo<br />
              Gia Anh
            </strong>
          </p>
        </div>
        {/* Ct 2 */}
        <div className="col-md-4">
          <p>
            <strong>
              Khnh Tng<br />
              Hi Phong<br />
              Khi Nguyn<br />
              Minh Sn<br />
              Minh Quang<br />
              ng Nhn<br />
              Bo Khang
            </strong>
          </p>
        </div>
        {/* Ct 3 */}
        <div className="col-md-4">
          <p>
            <strong>
              Gia Thi<br />
              Minh Khang<br />
              H An Nhin<br />
              T An Nhin<br />
              Linh an<br />
              Hu <br />
              Phc Khang<br />
              Khi Minh
            </strong>
          </p>
        </div>
      </div>

    
      <div
      className="container py-5"
      style={{
        backgroundColor: '#001f3f', // Nn mu xanh m
        borderRadius: '10px',
        padding: '30px',
        boxShadow: '0 8px 15px rgba(0, 0, 0, 0.3)',
      }}
    >
      {/* Tiu  "Cc vn u" */}
      <h2 className="text-center text-warning fw-bold mb-4">Cc vn u</h2>

      {/* Nt chuyn i vn u */}
      <div className="d-flex justify-content-center mb-4">
        <div className="btn-group" role="group">
          <button
            type="button"
            className={`btn ${activeTable === 1 ? 'btn-primary' : 'btn-success'} mx-1`}
            onClick={() => setActiveTable(1)}
          >
            Vn 1
          </button>
          <button
            type="button"
            className={`btn ${activeTable === 2 ? 'btn-primary' : 'btn-success'} mx-1`}
            onClick={() => setActiveTable(2)}
          >
            Vn 2
          </button>
        </div>
      </div>

      {/* Hin th bng da trn trng thi */}
      {activeTable === 1 && (
        <table className="table table-dark table-hover table-bordered text-center shadow-lg mb-0">
          <thead className="thead-dark">
            <tr>
              <th>BN</th>
              <th>QUN TRNG</th>
              <th>KT QU</th>
              <th>QUN EN</th>
            </tr>
          </thead>
          <tbody>
            {matchesTable1.map((match, index) => (
              <tr key={index} style={{ height: '3rem' }}>
                <td>{match.board}</td>
                <td>{match.white}</td>
                <td>{match.result}</td>
                <td>{match.black}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}

      {activeTable === 2 && (
        <table className="table table-dark table-hover table-bordered text-center shadow-lg mb-0">
          <thead className="thead-dark">
            <tr>
              <th>BN</th>
              <th>QUN TRNG</th>
              <th>KT QU</th>
              <th>QUN EN</th>
            </tr>
          </thead>
          <tbody>
            {matchesTable2.map((match, index) => (
              <tr key={index} style={{ height: '3rem' }}>
                <td>{match.board}</td>
                <td>{match.white}</td>
                <td>{match.result}</td>
                <td>{match.black}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>

    <div
      className="container py-5 mt-3"
      style={{
        backgroundColor: '#001f2f', // Nn xanh m
        borderRadius: '10px',
        padding: '30px',
        boxShadow: '0 8px 15px rgba(0, 0, 0, 0.3)',
      }}
    >
      {/* Tiu  bng */}
      <h2 className="text-center text-primary fw-bold mb-4">Bng Xp Hng</h2>

      {/* Bng xp hng */}
      <table className="table table-dark table-bordered text-center">
        <thead>
          <tr style={{ backgroundColor: '#FB4141', color: '#fff' }}>
            <th>HNG</th>
            <th>TN</th>
            <th>IM</th>
          </tr>
        </thead>
        <tbody>
          {rankings.map((player, index) => (
            <tr key={index} style={index % 2 === 0 ? { backgroundColor: '#2c2c2c' } : {}}>
              <td>{player.rank}</td>
              <td>{player.name}</td>
              <td>{player.points}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  
    </div>
<Footer/>

    </div>
  );
};

export default TournamentDetails;
