import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import "../css/LogoCarousel.css";

const LogoCarousel = () => {
  const logos = [
    { src: "https://covuadaisy.com/wp-content/uploads/2024/04/broad.png", alt: "Broad" },
    { src: "https://covuadaisy.com/wp-content/uploads/2024/04/duy-tan.png", alt: "Duy Tn" },
    { src: "https://covuadaisy.com/wp-content/uploads/2024/04/Nguyen-Binh-Khiem.png", alt: "Nguyn Bnh Khim" },
    { src: "https://covuadaisy.com/wp-content/uploads/2024/04/O-re-ca.png", alt: "O-re-ca" },
    { src: "https://covuadaisy.com/wp-content/uploads/2024/04/cung-thieu-nhi.png", alt: "Cung Thiu Nhi" },
  ];

  const settings = {
    dots: false, // n du chm ch bo
    infinite: true, // Cho php lp v hn
    speed: 500, // Tc  chuyn slide
    slidesToShow: 4, // Hin th 4 logo trn mi slide
    slidesToScroll: 1, // Ch cun 1 logo mi ln
    autoplay: true, // T ng lt
    autoplaySpeed: 3000, // Tc  lt t ng
    responsive: [
      {
        breakpoint: 768, // Khi mn hnh nh hn 768px
        settings: {
          slidesToShow: 2, // Hin th 2 logo trn mi slide
          slidesToScroll: 1, // Cun tng logo
        },
      },
      {
        breakpoint: 576, // Khi mn hnh nh hn 576px
        settings: {
          slidesToShow: 1, // Hin th 1 logo trn mi slide
          slidesToScroll: 1, // Cun tng logo
        },
      },
    ],
  };

  return (
    <section className="py-5 bg-light w-100">
      <div className="container">
        <Slider {...settings}>
          {logos.map((logo, index) => (
            <div key={index} className="text-center">
              <img
                src={logo.src}
                alt={logo.alt}
                className="img-fluid logo-image"
              />
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

export default LogoCarousel;
