import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { useNavigate } from "react-router-dom"; // S dng iu hng React Router
import Header from "../Component/Header";
import Footer from "../Component/Footer";

const LoginForm = () => {
  const navigate = useNavigate();

  // D liu ngi dng mu
  const users = [
    { username: "admin", password: "admin123", role: "admin" },
    { username: "user", password: "user123", role: "user" },
    { username: "teachers", password: "teachers", role: "teacher" },
  ];

  // State
  const [formData, setFormData] = useState({ username: "", password: "" });
  const [error, setError] = useState("");

  // X l thay i input
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // X l ng nhp
  const handleLogin = (e) => {
    e.preventDefault();
  
    const user = users.find(
      (u) =>
        u.username === formData.username && u.password === formData.password
    );
  
    if (user) {
      // Lu thng tin ngi dng vo localStorage
      localStorage.setItem("loggedInUser", JSON.stringify(user));
  
      // iu hng theo vai tr
      if (user.role === "admin" || user.role === "teacher") {
    

        navigate("/admin");
        window.location.reload();
      } else if (user.role === "user") {
       

        navigate("/profile");
        window.location.reload();
      }
    } else {
      setError("Tn ng nhp hoc mt khu khng chnh xc!");
    }
  };
  

  return (
    <div>
      <Header />
      <div className="container-fluid d-flex justify-content-center align-items-center vh-100">
        <div
          className="card shadow p-4"
          style={{ maxWidth: "400px", width: "100%" }}
        >
          <h3 className="text-center mb-4">Xin cho, Cho mng tr li!</h3>
          <form onSubmit={handleLogin}>
            {/* Tn ng nhp / Email */}
            <div className="mb-3">
              <input
                type="text"
                className="form-control"
                placeholder="Tn ng nhp"
                name="username"
                value={formData.username}
                onChange={handleChange}
                required
              />
            </div>

            {/* Mt khu */}
            <div className="mb-3">
              <input
                type="password"
                className="form-control"
                placeholder="Mt khu"
                name="password"
                value={formData.password}
                onChange={handleChange}
                required
              />
            </div>

            {/* Li */}
            {error && <div className="text-danger mb-3">{error}</div>}

            {/* Nt ng nhp */}
            <button type="submit" className="btn btn-primary w-100">
              ng nhp
            </button>

            {/* ng k */}
            <div className="text-center mt-3">
              <span>Cha c ti khon?&nbsp;</span>
              <a
                href="/registration"
                className="text-primary text-decoration-none"
              >
                ng K Ngay
              </a>
            </div>
          </form>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default LoginForm;
